<script>

export default {
    name: 'my-slider',
    props: {
        list: {
            type: Array,
            requier: true
        },
        name: String
    },
    data() {
        return {
            position: 0,
            width: false,
            points: [],

        }
    },
    mounted() {
        this.width = document.querySelector('body').clientWidth;
        this.returnIndex(0);
    },
    methods: {
        returnIndex(name) {
            this.points = document.querySelectorAll(`.slider__point-${name}`);
            let id = -(this.position / this.width)
            for (let i = 0; i < this.points.length; i++) {
                this.points[i].classList.remove('point-active');
                if (i === id) {
                    this.points[i].classList.add('point-active');
                }
            }

        },
        slidePrev(name) {
            if (!this.position - this.width == `-${this.width}`) {
                this.position += this.width;
            }
            document.querySelector(`.slider__trap-${name}`).style.transform = `translateX(${this.position}px)`;
            this.returnIndex(name);
        },
        slideNext(name) {
            let length = document.querySelectorAll(`.slider__card-${name}`).length;
            if (this.position - this.width > -this.width * length) {
                this.position -= this.width;
            }

            document.querySelector(`.slider__trap-${name}`).style.transform = `translateX(${this.position}px)`;
            this.returnIndex(name);
        },
        toSlide(name) {
            this.points = document.querySelectorAll(`.slider__point-${name}`);
            let point = event.target;
            let id;
            for (let i = 0; i < this.points.length; i++) {
                if (point === this.points[i]) {
                    id = i;
                }
            }
            if (id || id === 0) {
                this.position = 0;
                this.position = -Number(this.width) * Number(id);

                document.querySelector(`.slider__trap-${name}`).style.transform = `translateX(${this.position}px)`;
                this.returnIndex(name);
            }
        }
    },
}
</script>

<template>
    <div class="slider">
        <div class="slider__container container">
            <div class="slider__trap" :class="`slider__trap-${name}`">
                <div class="slider__card" :class="`slider__card-${name}`" v-for="item in list">
                    <div class="slider__body" :class="`slider__body-${name}`">
                        <span class="slider__title">{{ item.title }}</span>
                        <p class="slider__description">{{ item.text }}</p>
                        <my-button class="slider__button" v-if="name == 'home'">Перейти</my-button>
                    </div>
                    <img :src="require(`../../assets/img/slider/${item.name}/slider__item${item.index}.jpg`)"
                        class="slider__background" :class="`slider__background-${name}`" />

                </div>
            </div>
        </div>
        <img src="@/assets/img/slider/arrow-right.png" class="slider__row slider__row-left" @click="slidePrev(name)">
        <img src="@/assets/img/slider/arrow-right.png" class="slider__row slider__row-right" @click="slideNext(name)">
        <div class="slider__points" :class="`slider__points-${name}`">
            <div class="slider__point" :class="`slider__point-${name}`" v-for="item in list" @click="toSlide(name)"></div>
        </div>
    </div>
</template>

<style lang='scss' scoped>
@use '../../assets/styles/vars.scss' as v;
@use '../../assets/styles/fonts' as f;
@use '../../assets/styles/class.scss' as c;

.slider {
    & {
        height: 600px;

        position: relative;

        @media (max-width: 576px) {
            height: 400px;
        }
    }

    &__container {
        overflow: hidden;
    }

    &__row {
        cursor: pointer;
        position: absolute;
        z-index: 3;

        &:hover {
            transition: .4s;
            filter: brightness(80%);
        }

        &:active {
            transition: .2s;
            filter: brightness(70%);
        }

        @media (max-width: 576px) {
            width: 30px;
        }
    }

    &__row-left {
        top: 40%;
        left: 0;
        transform: rotate(180deg);
    }

    &__row-right {
        top: 40%;
        right: 0;

    }

    &__background {

        & {
            background-position: center center;
            background-repeat: no-repeat;
            background-attachment: fixed;
            background-size: cover;
        }

        &-home {
            position: absolute;
            filter: brightness(45%);
            width: 100vw;
            height: 100%;
            object-fit: cover;
        }

        &-feed {
            position: absolute;
            filter: brightness(75%);
            height: 100%;
            object-fit: cover;

            @media (min-width: 1024px){
                width: 30vw; 
            }
            @media (max-width: 1024px){
                width: 80vw; 
                filter: brightness(45%);
            }
            @media (max-width: 576px){
                width: 100vw; 
            }
        }
    }

    &__trap {
        position: absolute;
        inset: 0;
        display: flex;
        transition: .8s;
    }

    &__card {
        display: flex;
        justify-content: center;
        height: 100%;
        width: 100vw;
        flex: 1 0 100%;
        position: relative;
        background-color: v.$light-carnation-back-d;
        overflow: hidden;
    }

    &__body {

        & {
            position: absolute;
            display: flex;
            flex-direction: column;
        }

        &-home {
            bottom: 0;
            left: 0;
            z-index: 3;
            margin-bottom: 20px;
            margin-left: 20px;

            @media (max-width: 576px) {
                margin-bottom: 30px;
                margin-left: 15px;
            }
        }

        &-feed {
            bottom: 20%;
            left: 10%;
            padding: 20px;
            z-index: 3;

            @media (max-width: 576px){
                padding: 5px;
            }
        }

    }

    &__title {
        font-weight: 500;
        font-size: 48px;
        font-family: 'Nunito-Sans';
        font-weight: 500;
        color: v.$light-carnation;

        @media (max-width: 576px) {
            font-size: 28px;
        }
    }

    &__description {
        margin-top: 7px;
        margin-bottom: 40px;
        max-width: 400px;
        line-height: 25px;

        font-family: 'Nunito-Sans';
        font-weight: 400;
        color: v.$light-carnation;

        @media (max-width: 576px) {
            font-size: 17px;
            margin-top: 0px;
            margin-bottom: 20px;
            max-width: 250px;
        }
    }

    &__button {
        border: 1px solid rgba($color: v.$light-carnation, $alpha: 1.0);
        color: rgba($color: v.$light-carnation, $alpha: 1.0);

        align-self: flex-start;
        padding: 10px 50px;
        font-size: 20px;


        &:hover {
            background-color: rgba($color: v.$light-carnation, $alpha: 1.0);
            color: rgba($color: v.$grey, $alpha: 1.0);
        }

        &:active {
            background-color: rgba($color: v.$light-carnation-a, $alpha: 1.0);
        }

        @media (max-width: 576px) {
            font-size: 15px;
            padding: 7px 30px;
        }
    }

    &__points {
        position: absolute;
        display: flex;
        justify-content: center;
        gap: 5px;
        bottom: 15px;
        right: 50px;
        left: 50px;
        margin: 0 auto;
        z-index: 3;
    }

    &__point {
        background-color: rgba($color: v.$light-carnation, $alpha: 0.6);
        border-radius: 30%;
        width: 14px;
        height: 10px;

        transition: .2s;
        cursor: pointer;

        &:hover {
            background-color: rgba($color: v.$light-carnation, $alpha: 0.7);
        }

        &.point-active {
            background-color: rgba($color: v.$light-carnation, $alpha: 1);
        }
    }
}
</style>